import util from '../util'
import uuid from 'uuid'

const Mock = require('mockjs')
const Random = Mock.Random

// 
Mock.mock(/getRechargeOilProductsByType/, 'get', function (options) {
  switch (util.parseUrl(options.url, 'type').type) {
    case '1':
      return [{
          id: uuid(),
          integral: 5300,
          money: 50
        },
        {
          id: uuid(),
          integral: 10600,
          money: 100
        },
        {
          id: uuid(),
          integral: 21200,
          money: 200
        },
        {
          id: uuid(),
          integral: 53000,
          money: 500
        },
        {
          id: uuid(),
          integral: 106000,
          money: 1000
        }
      ]
    case '2':
      return []
    default:
      return []
  }
})

