Mock.mock(/getVideoProducts/, 'get', function (options) {
  const len = Random.natural(0, 10)
  const items = []

  for(let i = 0; i < len; i++) {
    items.push({
      productId: uuid(),
      buyPrice: Random.natural(1000, 100000),
      productName: "()"
    })
  }
  return {
    code: '1',
    items: items
  }
})